--prussian-blue: #1d3557ff; */

.recipe {
    padding: 0.25rem 0.5rem 0.25rem 0.5rem;
    background: white;
    transition: background 0.2s ease;
    margin-bottom: 0.5rem;
}

.recipe:hover {
    background: #f1faeeff;
}

.recipe__source {
    font-weight: 200;
    font-style: italic;
    color: #555;
    font-size: 0.875rem;
}

.results__result-count {
    padding: 0.5rem;
    font-weight: 200;
    font-style: italic;
    text-decoration: underline;
}
.search-field {
    padding: 1rem 0.5rem;
    background: #457b9dff;
}

.search-field__input {
    padding: 0.5rem;
    border: #1d3557ff;
    border-radius: 3px;
    width: 100%;
    max-width: 350px;
}

::-webkit-input-placeholder {
    font-weight: 200;
    font-style: italic;
    color: #888
}

:-ms-input-placeholder {
    font-weight: 200;
    font-style: italic;
    color: #888
}

::-ms-input-placeholder {
    font-weight: 200;
    font-style: italic;
    color: #888
}

::placeholder {
    font-weight: 200;
    font-style: italic;
    color: #888
}
